.nav {

  position: fixed;
  display: flex;
  bottom: 20px;
  right: 0;
  left: 0;
  height: 45px;
  padding: auto;
  justify-content: center;
  align-items: center;
  background-color: white;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
  border-bottom-left-radius: 20px;
  box-shadow: 0 0 10px 5px rgba(221, 221, 221, 1);
  margin-right: 50px;
  margin-left: 50px;

 
  }
.item{
  margin-right: auto;
  padding: 22px;
  margin: 0 -10px
}
.item a:focus{
fill: rgb(80,77,228);
stroke: rgb(80,77,228);
stroke-width: 0.5px;
position: relative;
bottom: 10px;
display: flex;
}
.item a:focus::after {
  content: "";
  position: absolute;
  bottom: -10px;
  left: 50%;
  transform: translateX(-50%);
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background-color: rgb(80,77,228)
}


