.descriptionBlock {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
  }
  
  .photoContainer {
    position: relative;
  }
  
  .photoCircle {
    position: absolute;
    right: 0;
    bottom: 0;
    width: 25px;
    height: 25px;
    display: flex;
    justify-content: center;
    align-items: center;


  }
  
  .mainPhoto {
    width: 100px;
    border-radius: 50%;
    object-fit: cover;
  }
  .addPhotoOverlay span {
    border: 2px solid white;
    display: block;
    width: 2rem;
    height: 2rem;
    border-radius: 50%;
    background-color: rgb(80, 77, 228);
    color: white;
    font-size: 19px;
    text-align: center;
    
  }
  .addPhotoOverlay input[type="file"] {
    display: none;
  }
  .name {
    font-size: 1.5rem;
    font-weight: bold;
    margin-top: 1rem;
  }
  
  .followers {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 1rem;
    color: #7d8aab;
    font-size: 14px;
  }
  
  .following, .follower {
    margin: 0 40px;
  }
  
  .aboutMe {
    margin-top: 1rem;
  }
  .ProfileStatusWithHooks_status{
    font-size: 14px;
  }